import React from "react"
import { makeStyles } from "@material-ui/styles"
import { Theme } from "@material-ui/core/styles"
import { Card, CardContent, Grid, Typography } from "@material-ui/core"
import { Poster, Project } from "../../../generated/ProjectPerYear"
import PosterTile from "./PosterTile"

const useStyles = makeStyles((theme: Theme) => ({}))

interface Props {
  project: Project
}

const ProjectCard: React.FC<Props> = ({ project }: Props) => {
  const classes = useStyles()
  return (
    <Card>
      <CardContent>
        <Typography variant={"h5"} component={"h2"} paragraph>
          {project.title}
        </Typography>
        {project.supervisors.length > 0 && (
          <Typography variant={"body1"} paragraph>
            Supervisors: {project.supervisors.join(", ")}
          </Typography>
        )}
        <Grid
          container
          direction="row"
          justify={"space-evenly"}
          alignItems={"flex-start"}
          spacing={0}
        >
          {project.posters.map((poster: Poster) => (
            <PosterTile key={poster.id} poster={poster} />
          ))}
        </Grid>
      </CardContent>
    </Card>
  )
}

export default ProjectCard
