import React from "react"
import { makeStyles } from "@material-ui/styles"
import { Theme } from "@material-ui/core/styles"
import NavAndSideBarLayout from "../../layout/NavAndSideBarLayout"
import {
  Poster,
  Project,
  ProjectContext,
} from "../../../generated/ProjectPerYear"
import PosterTile from "./PosterTile"
import ProjectCard from "./ProjectCard"
import { Grid } from "@material-ui/core"

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    display: "flex",
  },
}))

interface Props {
  pageContext: ProjectContext
}

const PosterOverview: React.FC<Props> = ({ pageContext }: Props) => {
  const classes = useStyles()
  return (
    <NavAndSideBarLayout
      slugsInfos={pageContext.slugsInfos}
      currentSlug={pageContext.slug}
    >
      <Grid container className={classes.root} justify="center" spacing={2}>
        {pageContext.projects.map((project: Project) => (
          <Grid item lg={10} key={project.id}>
            <ProjectCard project={project} />
          </Grid>
        ))}
      </Grid>
    </NavAndSideBarLayout>
  )
}

export default PosterOverview
